:root {
  --primary-color: #0050a0;
  --primary-dark: #003e7e;
  --error-color: #dc3545;
  --success-color: #28a745;
  --background-color: #f4f4f4;
  --text-color: #333;
  --border-radius: 4px;
}

body {
  text-align: center;
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.6;
}

:focus {
  outline: 3px solid var(--primary-color);
  outline-offset: 2px;
}

.skip-link {
  background-color: var(--primary-color);
  color: #fff;
  z-index: 100;
  padding: 8px;
  transition: top .3s;
  position: absolute;
  top: -40px;
  left: 0;
}

.skip-link:focus {
  top: 0;
}

.sr-only {
  clip: rect(0, 0, 0, 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

header {
  background-color: var(--primary-color);
  color: #fff;
  margin-bottom: 2rem;
  padding: 1rem;
  position: relative;
}

h1 {
  margin: 0;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 0 20px;
}

.signin-page {
  background: linear-gradient(135deg, #f4f4f4 0%, #e0e0e0 100%);
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.signin-container {
  width: 90%;
  max-width: 600px;
  padding: 2rem;
}

.signin-content {
  border-radius: var(--border-radius);
  text-align: center;
  background: #fff;
  padding: 3rem;
  box-shadow: 0 4px 6px #0000001a;
}

.tagline {
  color: #666;
  margin: 1rem 0 2rem;
  font-size: 1.2rem;
}

.features {
  text-align: left;
  margin: 2rem 0;
}

.features ul {
  padding: 0;
  list-style: none;
}

.features li {
  color: #444;
  margin: 1rem 0;
  font-size: 1.1rem;
}

.signin-box {
  border-radius: var(--border-radius);
  background: #f8f9fa;
  margin: 2rem 0;
  padding: 2rem;
}

.signin-button {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: var(--border-radius);
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 100%;
  max-width: 300px;
  margin: 1.5rem auto 0;
  padding: 12px 24px;
  font-size: 1.1rem;
  transition: transform .2s, background-color .2s;
  display: flex;
}

.signin-button:hover {
  background-color: var(--primary-dark);
  transform: translateY(-2px);
}

.google-icon {
  width: 24px;
  height: 24px;
}

.signin-footer {
  color: #666;
  margin-top: 2rem;
}

.privacy-note {
  color: #888;
  font-size: .9rem;
}

.form-group {
  text-align: left;
  margin-bottom: 1.5rem;
}

label {
  color: var(--text-color);
  margin-bottom: .5rem;
  font-weight: 500;
  display: block;
}

input[type="text"], select, textarea {
  border-radius: var(--border-radius);
  box-sizing: border-box;
  border: 1px solid #ccc;
  width: 100%;
  min-height: 44px;
  margin: 8px 0;
  padding: 10px;
  font-size: 1rem;
}

textarea {
  resize: vertical;
  height: 100px;
}

button {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: var(--border-radius);
  cursor: pointer;
  border: none;
  min-height: 44px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .3s;
}

button:hover:not(:disabled) {
  background-color: var(--primary-dark);
}

button:disabled {
  opacity: .7;
  cursor: not-allowed;
}

#signoutbttn {
  background-color: var(--error-color);
  z-index: 10;
  min-height: 36px;
  padding: 8px 16px;
  font-size: .9rem;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

#signoutbttn:hover {
  background-color: #c82333;
}

.filters {
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
  display: flex;
}

.books-list {
  margin-top: 2rem;
}

.book-item {
  border-radius: var(--border-radius);
  text-align: left;
  background: #fff;
  margin: 1rem 0;
  padding: 1.5rem;
  position: relative;
  box-shadow: 0 2px 4px #0000001a;
}

.book-item h3 {
  color: var(--primary-color);
  margin: 0 0 1rem;
}

.book-item p {
  margin: .5rem 0;
}

.delete-btn {
  background-color: var(--error-color);
  padding: 5px 10px;
  font-size: .9rem;
  position: absolute;
  top: 1rem;
  right: 1rem;
}

.delete-btn:hover {
  background-color: #c82333;
}

.error {
  border-color: var(--error-color);
  background-color: #fff8f8;
}

.error-message {
  color: var(--error-color);
  border-radius: var(--border-radius);
  background-color: #f8d7da;
  margin-top: .25rem;
  margin-bottom: .5rem;
  padding: .5rem;
  font-size: .875rem;
  display: block;
}

.success-message {
  color: var(--success-color);
  border-radius: var(--border-radius);
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  margin-bottom: 1rem;
  padding: .75rem 1.25rem;
}

.loading-spinner {
  vertical-align: middle;
  border: 2px solid #fff;
  border-top-color: #0000;
  border-radius: 50%;
  width: 1rem;
  height: 1rem;
  margin-right: .5rem;
  animation: 1s linear infinite spin;
  display: inline-block;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

#chatbot-container {
  border-radius: var(--border-radius);
  z-index: 1000;
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 15px;
  transition: transform .3s;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 2px 8px #0000001a;
}

#chatbot-container.minimized {
  transform: translateY(calc(100% + 20px));
}

.minimize-btn {
  color: #666;
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  min-height: 30px;
  padding: 5px 10px;
  font-size: 1.2rem;
  display: flex;
  position: absolute;
  top: 10px;
  right: 10px;
  background: none !important;
}

.minimize-btn:hover {
  color: #333;
  background: none !important;
}

.chat-toggle-btn {
  background-color: var(--primary-color);
  color: #fff;
  border-radius: var(--border-radius);
  cursor: pointer;
  z-index: 1000;
  border: none;
  padding: 10px 20px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 2px 8px #0003;
}

.chat-toggle-btn.visible {
  display: block;
}

.chat-toggle-btn:hover {
  background-color: var(--primary-dark);
}

#chat-history {
  border-bottom: 1px solid #eee;
  height: 300px;
  margin-top: 30px;
  margin-bottom: 15px;
  padding: 10px;
  overflow-y: auto;
}

.chat-input-group {
  gap: 8px;
  display: flex;
}

#chat-input {
  flex-grow: 1;
  min-height: 44px;
}

.history {
  border-radius: var(--border-radius);
  background-color: #f8f9fa;
  margin-bottom: 8px;
  padding: 8px;
}

.history:nth-child(odd) {
  background-color: #e9ecef;
}

.rating {
  color: gold;
  letter-spacing: 2px;
}

@media (width <= 768px) {
  .container {
    padding: 0 10px;
  }

  .filters {
    flex-direction: column;
    align-items: stretch;
  }

  select {
    width: 100%;
  }

  #chatbot-container {
    border-radius: var(--border-radius) var(--border-radius) 0 0;
    width: 100%;
    bottom: 0;
    left: 0;
    right: 0;
  }

  .chat-toggle-btn {
    border-radius: 0;
    width: 100%;
    bottom: 0;
    right: 0;
  }

  .signin-container {
    padding: 1rem;
  }

  .signin-content {
    padding: 1.5rem;
  }

  h1 {
    font-size: 1.5rem;
  }

  .book-item {
    padding: 1rem;
  }

  .delete-btn {
    width: 100%;
    margin-top: 1rem;
    position: static;
  }

  input, select, textarea, button {
    font-size: 16px;
  }

  #signoutbttn {
    padding: 6px 12px;
    font-size: .8rem;
    top: .5rem;
    right: .5rem;
  }
}

@media (prefers-contrast: high) {
  :root {
    --primary-color: #00e;
    --background-color: white;
    --text-color: black;
  }

  .book-item {
    border: 2px solid;
  }

  .error {
    border: 2px solid var(--error-color);
  }

  .success-message {
    border: 2px solid var(--success-color);
  }
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
    animation: none !important;
  }
}

@media print {
  .no-print {
    display: none;
  }

  body {
    background: #fff;
  }

  .book-item {
    break-inside: avoid;
    border: 1px solid #000;
    margin: 1rem 0;
  }
}
